.header-option {
  display: flex;
  flex-direction: column;
  align-items: center;
  color: grey;
  margin-right: 20px;
  cursor: pointer;
}

.header-option:hover {
  color: black;
}

.header-option__icon {
  object-fit: contain;
  height: 25px !important;
  width: 25px !important;
}

.header-option__title {
  margin: 3px 0 0 0;
  font-size: 12px;
  font-weight: 400;
}
