.header {
  position: sticky;
  top: 0;
  z-index: 2;
  display: flex;
  background-color: white;
  justify-content: space-evenly;
  border-bottom: 0.1px solid lightgray;
  padding: 10px 0;
  width: 100%;
}

.header__left {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.header__left > img {
  object-fit: contain;
  height: 40px;
  margin-right: 10px;
}

.header__search {
  padding: 10px;
  display: flex;
  align-items: center;
  border-radius: 5px;
  background-color: #eef3f8;
}

.header__search > input {
    outline: none;
    border: none;
    background: none;
}


.header__right{
  display: flex;
}