.login {
  display: grid;
  place-items: center;
  margin: 100px auto;
}

.login > img {
  object-fit: contain;
  height: 70px;
  margin: 20px auto;
}
.login > form {
  display: flex;
  flex-direction: column;
}
.login > form > input {
  width: 350px;
  height: 50px;
  font-size: 20px;
  padding-left: 10px;
  margin-bottom: 10px;
  border-radius: 5px;
}

.login > form > button {
  width: 350px;
  height: 50px;
  font-size: large;
  color: #fff;
  background-color: #0074b1;
  border-radius: 5px;
}

.login__register {
  color: #0177b7;
  cursor: pointer;
}

.login > p {
  margin-top: 20px;
}
