.sidebar {
  position: sticky;
  top: 80px;
  flex: 0.2;
  border-radius: 10px;
  text-align: center;
  height: fit-content;
}

.sidebar__avatar {
  margin-bottom: 10px;
}

.sidebar__top {
  display: flex;
  flex-direction: column;
  align-items: center;
  border: 1px solid lightgray;
  border-bottom: 0;
  border-top-left-radius: 10px;
  border-top-right-radius: 10px;
  background-color: white;
  padding-bottom: 10px;
}

.sidebar__top > img {
  margin-bottom: -20px;
  object-fit: cover;
  width: 100%;
  height: 60px;
  border-top-left-radius: 10px;
  border-top-right-radius: 10px;
}

.sidebar__top > h2 {
  font-size: 18px;
}

.sidebar__top > h4 {
  font-size: 12px;
  color: gray;
}

.sidebar__stats {
  padding: 10px;
  margin-bottom: 10px;
  border: 1px solid lightgray;
  border-bottom-left-radius: 10px;
  border-bottom-right-radius: 10px;
  background-color: white;
}

.sidebar__stat {
  margin-top: 10px;
  display: flex;
  justify-content: space-between;
  text-align: left;
}

.sidebar__stat > p {
  color: gray;
  font-size: 13px;
  font-weight: 600;
}

.sidebar__stat-number {
  color: #0a66c2 !important;
}

.sidebar__bottom{
  padding: 10px;
  margin-top: 10px;
  border: 1px solid lightgray;
  border-radius: 10px;
  background-color: white;
  text-align: left;
}

.sidebar__bottom > p {
  font-size: 13px;
  padding-bottom: 10px;
}

.sidebar__topic{
  display: flex;
  font-size: 13px;
  color: gray;
  font-weight: bolder;
  cursor: pointer;
  padding: 5px;
}

.sidebar__topic:hover{
  border-radius: 10px;
  background-color: whitesmoke;
  color: black;
}

.sidebar__hash{
  margin: 0 10px 0 5px;
}